@import "../../scss/variables";
@import "../../scss/mixins";

.gallery {
  margin: 109px 0px 104px 0px;
  @media (max-width: 769px) {
    margin: 76px 0px 100px 0px;
  }
  .container {
    min-width: 100%;
  }
  // .gallery__title-block
  &__title-block {
    max-width: 1200px;
    margin: 0px auto;
    margin-bottom: 140px;
    padding: 0 15px 20px 15px;
    @media (max-width: 769px) {
      text-align: center;
      margin-bottom: 40px;
    }
  }
}
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0px auto;
}

.gallery-title-block {
  // .gallery-title-block__title
  &__title {
    font-weight: 400;
    font-size: 45px;
    text-transform: uppercase;

    /* gradient */

    background: $primary-color;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
}
